@import url("https://fonts.googleapis.com/css?family=Russo+One");


body {
  user-select: none;
  font-family: Russo one;
  background-color: #8d8d8d;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#drum-machine {
  outline: 5px solid orange;
  position: relative;
  display: flex;
  flex-direction: column;
  width: 660px;
  text-align: center;
  background-color: #b3b3b3;
}

#padBank {
  width: 332px;
  height: 272px;
  margin: 20px auto ;
   }

.drum-pad {
  position: relative;
  float: left;
  width: 100px;
  height: 80px;
  margin-right: 10px;
  border-radius: 5px;
  padding-top: 35px;
  box-sizing: border-box;
  cursor: pointer; 
  
}

.drum-pad.on.inactive {
  background-color: grey;
  margin-top: 10px;
  box-shadow: 3px 3px 5px black;
}

.drum-pad.on.active {
  background-color: orange;
  box-shadow: 0 3px orange;
  height: 77px;
  margin-top: 13px;
}

.drum-pad.off.active {
  background-color: grey;
  box-shadow: 0 3px grey;
  height: 77px;
  margin-top: 13px;
}
.drum-pad.off.inactive {
  background-color: grey;
  margin-top: 10px;
  box-shadow: 3px 3px 5px black;
}

#display {
  width: 200px;
  height: 12px;
  background-color: gray;
  font-size: 15px;
  margin: 15px auto;
  padding: 10px;
}

#volume-control {
  width: 200px;
  cursor: pointer;
}

#switch {
  color: black;
}

#switch-icon {
  margin-left: 10px;
  margin-bottom: 10px;
}
